<template>
  <div
    class="text-center flex flex-row justify-between w-96 h-auto"
    :class="goalId % 2 !== 0 ? 'bg-gray-200' : 'bg-white'"
  >
    <div @dblclick="updateGoal(goalId)" class="goal flex flex-row text-xl">
      <p class="mr-4">{{ goalId }}.</p>
      <p id="goal_text">{{ goalText }}</p>
    </div>
    <i
      @click="this.$emit('close-goal', goalId)"
      class="text-red-500 fa fa-times"
    ></i>
  </div>
</template>
<script>
export default {
  name: "Goal",
  props: {
    goalId: {
      type: Number,
      default: 0,
    },
    goalText: {
      type: String,
      default: "Add New Goal...",
    },
  },
  methods: {
    /**
     * How to make goal updatable/editable:
     * 1. On double click, emit update event. In goals.vue, hide the p tag and show an input tag. Focus it.
     * The user will input the new goal into that and then click away to
     * trigger the data being sent up to App.vue, where it will edit the
     * data of this.goals where the id matches.
     *
     * 2. Add an input.focusout event listener. Save the data and send
     * it up the data flow using emits, where it will be saved in app.vue's
     * this.goal[] data. Hide the input tag and show the p tag.
     */
    updateGoal(goalId) {
      this.$emit("update-goal", goalId);

      // console.log(`Goal.vue goalId: ${goalId}`);
    },
  },
  emits: ["close-goal", "update-goal"],
};
</script>