<template>
  <h2 class="text-center text-sm">Warning: This page is not responsive.</h2>
  <h1 class="text-center text-2xl">Vue Goal Manager</h1>
  <!-- 
    add : to prop to use v-bind, which lets you use data from this components
    data function.
   -->
  <div class="my-5 w-full h-auto flex justif">
    <Button @add-goal="toggleForm" />
  </div>
  <NewGoalForm v-if="addGoal" @new-goal="addNewGoal" />
  <div class="flex flex-col mx-auto w-screen justify-center">
    <Goals @close-goal="deleteGoal" :goals="this.goals" />
    <!-- NOTE FOR EMITS: 
    You don't need to add any arguments to the method when it's called here,
    since the emittance automatically sends the data up to the method. Just use
    an argument in the method declaration down there in Methods and you'll have access to
    the data. 
    
    When the listener is not for an emittance, and it's a native listener, you DO 
    need to use arguments in the method call here, like what was done in Goal.vue
    with @DblCLick.
    -->
  </div>
</template>

<script>
import Goals from "./components/Goals";
import Button from "./components/Button";
import NewGoalForm from "./components/NewGoalForm";

export default {
  name: "App",
  data() {
    return {
      goals: [],
      addGoal: false, //addGoal form visibility.
    };
  },
  components: {
    Goals,
    Button,
    NewGoalForm,
  },
  methods: {
    toggleForm() {
      this.addGoal = !this.addGoal;
    },
    addNewGoal(newGoalText) {
      let newGoal = {
        id: this.goals.length + 1,
        text: newGoalText,
      };
      this.toggleForm();
      this.goals.push(newGoal);
    },
    deleteGoal(goalId) {
      this.goals = this.goals.filter((goal) => goal.id != goalId);
      this.goals.forEach((goal, i) => {
        goal.id = i + 1;
      });
    },
    updateGoal(goalId) {
      console.log(`App.Vue GoalId: ${goalId}`);
    },
    updatedGoal(updatedContent) {
      console.log(updatedContent);
      // this.goals[goalId].text = updatedContent;
    },
  },
  emits: ["close-goal"],
  created() {
    this.goals = [
      {
        id: 1,
        text: "Buy socks",
      },
      {
        id: 2,
        text: "Eat dinner",
      },

      {
        id: 3,
        text: "Water the Grass",
      },
    ];
  },
};
</script>
