<template>
  <div
    class="mb-12 w-96 px-6 h-auto bg-gray-100 rounded-lg shadow-lg border border-gray-100"
  >
    <form action="" class="flex flex-col">
      <label for="text" class="mt-8 mb-2 text-gray-500 font-semibold"
        >New Goal</label
      >
      <input
        class="rounded-lg border-gray-300 border shadow-sm py-2 pl-2 mb-6"
        type="text"
        name="text"
        id="new_goal_text"
      />
      <button
        @click="addNewGoal"
        type="submit"
        class="w-full mb-3 py-3 bg-indigo-500 text-white rounded-lg"
      >
        Add Goal
      </button>
    </form>
  </div>
</template>
<script>
export default {
  name: "NewGoalForm",
  methods: {
    addNewGoal(e) {
      e.preventDefault();
      // get form contents, then emit them. Then, in app.vue, append to goals array.
      let newGoalText = document.getElementById("new_goal_text").value;
      document.getElementById("new_goal_text").value = "";
      this.$emit("new-goal", newGoalText);
    },
  },
  emits: ["new-goal"],
};
</script>